/**
 * 
 * @type {{}}
 */
class EvalFunctions {

  /**
   * EvalFunctions
   */
  constructor() {
    this.functions = {};
  }

  /**
   * 
   * @param name
   */
  remove(name) {
    delete this.functions[name];
  }

  /**
   * 
   * @param name
   * @returns {*}
   */
  getFunction(name) {
    return this.functions[name.toLocaleString()];
  }

  /**
   * 
   * @param name
   * @param func
   */
  register(name, func) {
    this.functions[name.toLocaleString()] = func;
  }
}

/**
 * 
 * @type {EvalFunctions}
 */
const Functions = new EvalFunctions();

/**
 * 
 */
class Assembly {

  /**
   * Assembly
   */
  constructor() {
    this.assembly = '';
  }

  /**
   * 
   * @returns {string}
   */
  getInstruct() {
    let { assembly } = this;
    let flag = '\r\n';
    while (assembly.endsWith(flag)) {
      let { length } = assembly;
      assembly = assembly.substring(0, length - flag.length);
    }
    return assembly;
  }

  /**
   * 
   * @param value
   */
  writeOperand(value) {
    this.assembly += `OPush ${value}\r\n`;
  }

  /**
   * 
   * @param op
   */
  writeOperator(op) {
    this.assembly += `${op}\r\n`;
  }

  /**
   * 
   * @param value
   */
  writeString(value) {
    this.assembly += `SPush ${value}\r\n`;
  }

  /**
   * 
   * @param value
   */
  writeNumber(value) {
    this.assembly += `NPush ${value}\r\n`;
  }
}

/**
 * 
 */
class Tokenizer {

  /**
   * Tokenizer
   * @param syntaxCheck
   */
  constructor({
    syntaxCheck = true,
  } = {}) {
    this.syntaxCheck = syntaxCheck;
  }

  /**
   * 
   * @param input
   * @returns {*[]}
   */
  lexical(input) {
    let OPERATOR = /^(\+|-|\*|\/|%|>|<|,|\^|=|&|!|:|>=|<=|<>)$/;
    let NUMBERS = /[0-9.]/;
    let SPACE = /\s/;
    let LETTERS = /[a-z0-9_$]/i;
    // 
    let { length } = input;
    // 
    let bracketsStack = [];
    // 
    let current = 0;
    // 
    let tokens = [];
    // 
    while (current < length) {
      // 
      let char = input[current];
      // 
      if (SPACE.test(char)) {
        const next = input[current + 1];
        if (SPACE.test(next)) {
          current++;
          continue;
        }
      }
      // 
      if (SPACE.test(char)) {
        const last = input[current - 1];
        const next = input[current + 1];
        if (LETTERS.test(last) && LETTERS.test(next)) {
          tokens.push({
            type: 'operator',
            value: char,
          });
        }
        current++;
        continue;
      }
      // 
      if (char === '(') {
        tokens.push({
          type: 'brackets',
          value: '(',
        });
        bracketsStack.push({
          type: 'brackets',
          value: '(',
        });
        current++;
        continue;
      }
      if (char === ')') {
        const brackets = bracketsStack.pop();
        if (brackets && brackets.type === 'function') {
          tokens.push({
            type: 'function',
            value: ')',
          });
        } else {
          tokens.push({
            type: 'brackets',
            value: ')',
          });
        }
        current++;
        continue;
      }
      // 
      if (char === '{') {
        tokens.push({
          type: 'array',
          value: 'array',
        });
        tokens.push({
          type: 'array',
          value: '{',
        });
        current++;
        continue;
      }
      if (char === '}') {
        tokens.push({
          type: 'array',
          value: '}',
        });
        current++;
        continue;
      }
      // 
      if (char === '"') {
        let result = '';
        current++;
        char = input[current];
        while (char !== '"') {
          if (current < length) {
            result += char;
            current++;
            char = input[current];
          } else {
            break;
          }
        }
        current++;
        char = input[current];
        tokens.push({
          type: 'string',
          value: result,
        });
        continue;
      }
      if (char === '\'') {
        let result = '';
        current++;
        char = input[current];
        while (char !== '\'') {
          if (current < length) {
            result += char;
            current++;
            char = input[current];
          } else {
            break;
          }
        }
        current++;
        char = input[current];
        tokens.push({
          type: 'string',
          value: result,
        });
        continue;
      }
      // 
      if (OPERATOR.test(char)) {
        let next = input[current + 1];
        let result = `${char}${next}`;
        if (OPERATOR.test(result)) {
          tokens.push({
            type: 'operator',
            value: result,
          });
          current++;
        } else {
          tokens.push({
            type: 'operator',
            value: char,
          });
        }
        current++;
        continue;
      }
      // 
      if (NUMBERS.test(char)) {
        let result = '';
        while (NUMBERS.test(char)) {
          if (current < length) {
            result += char;
            current++;
            char = input[current];
          } else {
            break;
          }
        }
        tokens.push({
          type: 'number',
          value: result,
        });
        continue;
      }
      // , , 
      if (LETTERS.test(char)) {
        let result = '';
        while (LETTERS.test(char)) {
          if (current < length) {
            result += char;
            current++;
            char = input[current];
          } else {
            break;
          }
        }
        // 
        if (char === '(') {
          tokens.push({
            type: 'function',
            value: result,
          });
          tokens.push({
            type: 'function',
            value: '(',
          });
          bracketsStack.push({
            type: 'function',
            value: '(',
          });
          current++;
        } else {
          tokens.push({
            type: 'operand',
            value: result,
          });
        }
        continue;
      }
      // token
      throw new TypeError(`token ${char} `);
    }
    // 
    if (this.syntaxCheck) {
      return this.syntax(tokens);
    }
    return tokens;
  }

  /**
   * 
   * @param tokens
   */
  syntax(tokens) {
    // 
    let sQuotesStack = [];
    // 
    let dQuotesStack = [];
    // 
    let callStack = [];
    // 
    let arrayStack = [];
    // 
    let bracketsStack = [];
    // 
    let current = 0;
    // tokens
    let { length } = tokens;
    // token
    let ignoreArray = (token) => {
      if (token.type === 'array') {
        return token.value === 'array';
      }
      return false;
    };
    // token
    let ignoreFunc = (token) => {
      if (token.type === 'function') {
        return token.value !== '(' && token.value !== ')';
      }
      return false;
    };
    // 
    let skipIgnoreToken = (dir, index) => {
      if (dir === 'last') {
        while (index >= 0) {
          let token = tokens[index];
          if (ignoreFunc(token)) {
            index--;
            continue;
          }
          if (ignoreArray(token)) {
            index--;
            continue;
          }
          return token;
        }
      }
      if (dir === 'next') {
        while (index < length) {
          let token = tokens[index];
          if (ignoreFunc(token)) {
            index++;
            continue;
          }
          if (ignoreArray(token)) {
            index++;
            continue;
          }
          return token;
        }
      }
      return null;
    };
    // 
    let checkArray = (dir, target) => {
      let token = tokens[current];
      let local = dir === 'last' ? '' : '';
      if (token.value === '{') {
        if (dir === 'last') {
          switch (target.type) {
            case 'array': {
              if (target.value === '{') {
                return;
              }
              break;
            }
            case 'function': {
              if (target.value === '(') {
                return;
              }
              break;
            }
            case 'brackets': {
              if (target.value === '(') {
                return;
              }
              break;
            }
            case 'operator': {
              if (target.value === ',') {
                return;
              }
              return;
            }
          }
        }
        if (dir === 'next') {
          switch (target.type) {
            case 'string':
            case 'array':
            case 'number':
            case 'operand': {
              return;
            }
            case 'function': {
              if (target.value === '(') {
                return;
              }
              break;
            }
            case 'brackets': {
              if (target.value === '(') {
                return;
              }
              break;
            }
          }
        }
      }
      if (token.value === '}') {
        if (dir === 'last') {
          switch (target.type) {
            case 'number':
            case 'string':
            case 'array':
            case 'operand': {
              return;
            }
            case 'function': {
              if (target.value === ')') {
                return;
              }
              break;
            }
            case 'brackets': {
              if (target.value === ')') {
                return;
              }
              break;
            }
          }
        }
        if (dir === 'next') {
          switch (target.type) {
            case 'array': {
              if (target.value === '}') {
                return;
              }
              break;
            }
            case 'function': {
              if (target.value === ')') {
                return;
              }
              break;
            }
            case 'brackets': {
              if (target.value === ')') {
                return;
              }
              break;
            }
            case 'operator': {
              if (target.value === ',') {
                return;
              }
              return;
            }
          }
        }
      }
      throw TypeError(` ${token.value} ${local} ${target.value}`);
    };
    // 
    let checkValue = (dir, target) => {
      let token = tokens[current];
      let local = dir === 'last' ? '' : '';
      switch (target.type) {
        case 'array': {
          if (dir === 'last') {
            if (target.value === '{') {
              return;
            }
          }
          if (dir === 'next') {
            if (target.value === '}') {
              return;
            }
          }
          break;
        }
        case 'brackets': {
          if (dir === 'last') {
            if (target.value === '(') {
              return;
            }
          }
          if (dir === 'next') {
            if (target.value === ')') {
              return;
            }
          }
          break;
        }
        case 'function': {
          if (dir === 'last') {
            if (target.value === '(') {
              return;
            }
          }
          if (dir === 'next') {
            if (target.value === ')') {
              return;
            }
          }
          break;
        }
        case 'operator': {
          return;
        }
      }
      throw TypeError(` ${token.value} ${local} ${target.value}`);
    };
    // 
    let checkOperator = (dir, target) => {
      let token = tokens[current];
      let local = dir === 'last' ? '' : '';
      switch (target.type) {
        case 'brackets':
        case 'function':
        case 'string':
        case 'number':
        case 'operand': {
          return;
        }
        case 'array': {
          if (token.value === ',') {
            return;
          }
          break;
        }
      }
      throw TypeError(` ${token.value} ${local} ${target.value}`);
    };
    // 
    let checkFunction = (dir, target) => {
      let token = tokens[current];
      let local = dir === 'last' ? '' : '';
      if (token.value === '(') {
        if (dir === 'last') {
          switch (target.type) {
            case 'array': {
              if (target.value === '{') {
                return;
              }
              break;
            }
            case 'function': {
              if (target.value === '(') {
                return;
              }
              break;
            }
            case 'brackets': {
              if (target.value === '(') {
                return;
              }
              break;
            }
            case 'operator': {
              return;
            }
          }
        }
        if (dir === 'next') {
          switch (target.type) {
            case 'function':
            case 'string':
            case 'number':
            case 'operand': {
              return;
            }
            case 'array': {
              if (target.value === '{') {
                return;
              }
              break;
            }
            case 'brackets': {
              if (target.value === '(') {
                return;
              }
              break;
            }
          }
        }
      }
      if (token.value === ')') {
        if (dir === 'last') {
          switch (target.type) {
            case 'function':
            case 'number':
            case 'string':
            case 'operand': {
              return;
            }
            case 'array': {
              if (target.value === '}') {
                return;
              }
              break;
            }
            case 'brackets': {
              if (target.value === ')') {
                return;
              }
              break;
            }
          }
        }
        if (dir === 'next') {
          switch (target.type) {
            case 'array': {
              if (target.value === '}') {
                return;
              }
              break;
            }
            case 'function': {
              if (target.value === ')') {
                return;
              }
              break;
            }
            case 'brackets': {
              if (target.value === ')') {
                return;
              }
              break;
            }
            case 'operator': {
              if (target.value === ',') {
                return;
              }
              return;
            }
          }
        }
      }
      throw TypeError(` ${token.value} ${local} ${target.value}`);
    };
    // token
    while (current < length) {
      // token
      let token = tokens[current];
      // token
      if (ignoreArray(token)) {
        current++;
        continue;
      }
      if (ignoreFunc(token)) {
        current++;
        continue;
      }
      let { type, value } = token;
      // 
      if (type === "'") {
        if (sQuotesStack.length) {
          sQuotesStack.pop();
        } else {
          // 
          if (!dQuotesStack.length) {
            sQuotesStack.push("'");
          }
        }
      }
      // 
      if (type === '"') {
        if (dQuotesStack.length) {
          dQuotesStack.pop();
        } else {
          // 
          if (!sQuotesStack.length) {
            dQuotesStack.push('"');
          }
        }
      }
      // 
      if (type === 'string') {
        let last = skipIgnoreToken('last', current - 1);
        let next = skipIgnoreToken('next', current + 1);
        if (last) {
          checkValue('last', last);
        }
        if (next) {
          checkValue('next', next);
        }
      }
      // 
      if (type === 'number') {
        let last = skipIgnoreToken('last', current - 1);
        let next = skipIgnoreToken('next', current + 1);
        if (last) {
          checkValue('last', last);
        }
        if (next) {
          checkValue('next', next);
        }
      }
      // 
      if (type === 'array') {
        if (value === '{') {
          arrayStack.push(token);
        }
        if (value === '}') {
          if (arrayStack.length === 0) {
            throw new TypeError('');
          }
          arrayStack.pop();
        }
        let last = skipIgnoreToken('last', current - 1);
        let next = skipIgnoreToken('next', current + 1);
        if (last) {
          checkArray('last', last);
        }
        if (next) {
          checkArray('next', next);
        }
      }
      // 
      if (type === 'function') {
        if (value === '(') {
          callStack.push(token);
        }
        if (value === ')') {
          callStack.pop();
        }
        let last = skipIgnoreToken('last', current - 1);
        let next = skipIgnoreToken('next', current + 1);
        if (last) {
          checkFunction('last', last);
        }
        if (next) {
          checkFunction('next', next);
        }
      }
      // 
      if (type === 'operator') {
        let last = skipIgnoreToken('last', current - 1);
        let next = skipIgnoreToken('next', current + 1);
        // 
        if (last) {
          checkOperator('last', last);
        } else {
          throw TypeError(`: ${token.value} `);
        }
        if (next) {
          checkOperator('next', next);
        } else {
          throw TypeError(`: ${token.value} `);
        }
      }
      // 
      if (type === 'brackets') {
        if (value === '(') {
          bracketsStack.push(token);
        }
        if (value === ')') {
          if (bracketsStack.length === 0) {
            throw new TypeError('');
          }
          bracketsStack.pop();
        }
      }
      // 
      current++;
    }
    // 
    if (arrayStack.length) {
      throw new TypeError('');
    }
    if (callStack.length) {
      throw new TypeError('');
    }
    if (sQuotesStack.length) {
      throw new TypeError('');
    }
    if (dQuotesStack.length) {
      throw new TypeError('');
    }
    if (bracketsStack.length) {
      throw new TypeError('');
    }
    return tokens;
  }
}

/**
 * 
 */
class Compiler {

  /**
   * Compiler
   * @param tokens
   * @param writer
   */
  constructor({
    tokens, writer,
  } = {}) {
    this.writer = writer;
    this.tokens = tokens;
    this.groupStack = [];
    this.index = -1;
    this.ignore = [','];
    // 
    this.opIf = {
      '>': 'ifge',
      '<': 'ifgt',
      '=': 'ifeq',
      '>=': 'if_icmpge',
      '<=': 'if_icmpgt',
      '<>': 'ifne',
    };
    this.opAdd = {
      '+': 'add',
      '-': 'sub',
      '&': 'link',
    };
    this.opMul = {
      '*': 'mul',
      '/': 'div',
      '%': 'mod',
      '^': 'power',
    };
    this.opCommon = {
      ' ': 'common',
    };
    this.opRel = {
      '!': 'rel',
      ':': 'uni',
    };
    this.opNew = {
      array: 'newarray',
    };
    this.opCell = {
      function: 'invoke',
    };
  }

  /**
   * 
   */
  eofToken() {
    const { index, tokens } = this;
    const { length } = tokens;
    return index >= length - 1;
  }

  /**
   * Token
   */
  nextToken() {
    this.index++;
    this.token = this.tokens[this.index];
    return this.token;
  }

  /**
   * Token
   */
  backToken() {
    this.index--;
    this.token = this.tokens[this.index];
    return this.token;
  }

  /**
   * 
   */
  reduceGroup() {
    const { groupStack } = this;
    const { length } = groupStack;
    if (length > 0) {
      const last = groupStack[length - 1];
      const number = last.number;
      last.number = number - 1;
    }
  }

  /**
   * 
   */
  increaseGroup() {
    const { groupStack } = this;
    const { length } = groupStack;
    if (length > 0) {
      const last = groupStack[length - 1];
      const number = last.number;
      last.number = number + 1;
    }
  }

  /**
   * 
   */
  popGroup() {
    return this.groupStack.pop();
  }

  /**
   * 
   */
  addGroup(token) {
    token.number = 0;
    this.groupStack.push(token);
    return token;
  }

  /**
   * Token
   * @returns {*}
   */
  isSkipNextToken() {
    if (!this.eofToken()) {
      const { ignore } = this;
      this.nextToken();
      const { token } = this;
      this.backToken();
      const { value } = token;
      return ignore.includes(value);
    }
    return false;
  }

  /**
   * 
   */
  compile() {
    const { writer } = this;
    this.compileIfge();
    while (this.isSkipNextToken()) {
      this.nextToken();
      this.compileIfge();
    }
    return writer.getInstruct();
  }

  /**
   * 
   */
  compileIfge() {
    this.compileAdd();
    while (!this.eofToken()) {
      const token = this.nextToken();
      const { type, value } = token;
      if (type !== 'operator') {
        this.backToken();
        break;
      }
      if (!this.opIf[value]) {
        this.backToken();
        break;
      }
      let operator = this.opIf[value];
      this.compileAdd();
      this.reduceGroup();
      this.writer.writeOperator(operator);
    }
  }

  /**
   * 
   */
  compileAdd() {
    this.compileMul();
    while (!this.eofToken()) {
      const token = this.nextToken();
      const { type, value } = token;
      if (type !== 'operator') {
        this.backToken();
        break;
      }
      if (!this.opAdd[value]) {
        this.backToken();
        break;
      }
      let operator = this.opAdd[value];
      this.compileMul();
      this.reduceGroup();
      this.writer.writeOperator(operator);
    }
  }

  /**
   * 
   */
  compileMul() {
    this.compileCommon();
    while (!this.eofToken()) {
      const token = this.nextToken();
      const { type, value } = token;
      if (type !== 'operator') {
        this.backToken();
        break;
      }
      if (!this.opMul[value]) {
        this.backToken();
        break;
      }
      let operator = this.opMul[value];
      this.compileCommon();
      this.reduceGroup();
      this.writer.writeOperator(operator);
    }
  }

  /**
   * 
   */
  compileCommon() {
    this.compileRel();
    while (!this.eofToken()) {
      const token = this.nextToken();
      const { type, value } = token;
      if (type !== 'operator') {
        this.backToken();
        break;
      }
      if (!this.opCommon[value]) {
        this.backToken();
        break;
      }
      let operator = this.opCommon[value];
      this.compileRel();
      this.reduceGroup();
      this.writer.writeOperator(operator);
    }
  }

  /**
   * Sheet
   */
  compileRel() {
    this.compileArray();
    while (!this.eofToken()) {
      const token = this.nextToken();
      const { type, value } = token;
      if (type !== 'operator') {
        this.backToken();
        break;
      }
      if (!this.opRel[value]) {
        this.backToken();
        break;
      }
      let operator = this.opRel[value];
      this.compileArray();
      this.reduceGroup();
      this.writer.writeOperator(operator);
    }
  }

  /**
   * 
   */
  compileArray() {
    this.compileFunction();
    while (!this.eofToken()) {
      const token = this.nextToken();
      const { type, value } = token;
      if (type !== 'array') {
        this.backToken();
        break;
      }
      //  } 
      if (value === '}') {
        this.backToken();
        break;
      }
      this.addGroup(token);
      //  {  
      this.nextToken();
      // 
      this.compileIfge();
      // 
      // 
      while (this.isSkipNextToken()) {
        this.nextToken();
        this.compileIfge();
      }
      // 
      // 
      this.popGroup();
      const { number } = token;
      const operator = this.opNew[type];
      this.writer.writeOperator(`${operator} ${number}`);
      //  }
      this.nextToken();
    }
  }

  /**
   * 
   */
  compileFunction() {
    this.compilePushValue();
    while (!this.eofToken()) {
      const token = this.nextToken();
      const { type, value } = token;
      if (type !== 'function') {
        this.backToken();
        break;
      }
      //  ) 
      if (value === ')') {
        this.backToken();
        break;
      }
      this.addGroup(token);
      //  (  
      this.nextToken();
      // 
      this.compileIfge();
      // 
      // 
      while (this.isSkipNextToken()) {
        this.nextToken();
        this.compileIfge();
      }
      // 
      // 
      this.popGroup();
      const { number } = token;
      const operator = this.opCell[type];
      this.writer.writeOperator(`${operator} ${value} ${number}`);
      //  )
      this.nextToken();
    }
  }

  /**
   * compilePushValue
   */
  compilePushValue() {
    if (!this.eofToken()) {
      const token = this.nextToken();
      const { writer } = this;
      const { type, value } = token;
      // 
      // /()
      switch (type) {
        case 'string': {
          this.increaseGroup();
          writer.writeString(`${value}`);
          return;
        }
        case 'number': {
          this.increaseGroup();
          writer.writeNumber(`${value}`);
          return;
        }
        case 'operand': {
          this.increaseGroup();
          writer.writeOperand(value);
          return;
        }
      }
      // 
      switch (type) {
        case 'brackets': {
          if (value === '(') {
            this.compileIfge();
            // 
            // 
            while (this.isSkipNextToken()) {
              this.nextToken();
              this.compileIfge();
            }
            //  )
            this.nextToken();
          }
          return;
        }
      }
      // /()
      switch (type) {
        case 'array': {
          // {} 
          if (value !== '}') {
            this.increaseGroup();
          }
          this.backToken();
          return;
        }
        case 'function': {
          // func() 
          if (value !== ')') {
            this.increaseGroup();
          }
          this.backToken();
          return;
        }
      }
      // 
      switch (type) {
        case 'operator': {
          this.backToken();
          return;
        }
      }
      // token
      throw new TypeError(`token ${type} ${value} `);
    }
  }
}

/**
 * 
 */
class Evaluation {

  /**
   * Evaluate
   * @param instruct
   */
  constructor(instruct) {
    this.instruct = instruct.split('\r\n');
    this.memory = [];
  }

  /**
   * 
   */
  eval() {
    const newArray = /^newarray/;
    const invoke = /^invoke/;
    const pushSRegexp = /^SPush/;
    const pushNRegexp = /^NPush/;
    const pushORegexp = /^OPush/;
    const { instruct } = this;
    instruct.forEach((name) => {
      switch (name) {
        case 'if_icmpge': {
          this.ificmpge();
          break;
        }
        case 'if_icmpgt': {
          this.ificmpgt();
          break;
        }
        case 'ifge': {
          this.ifge();
          break;
        }
        case 'ifgt': {
          this.ifgt();
          break;
        }
        case 'ifeq': {
          this.ifeq();
          break;
        }
        case 'ifne': {
          this.ifne();
          break;
        }
        case 'add': {
          this.add();
          break;
        }
        case 'sub': {
          this.sub();
          break;
        }
        case 'mul': {
          this.mul();
          break;
        }
        case 'div': {
          this.div();
          break;
        }
        case 'mod': {
          this.mod();
          break;
        }
        case 'power': {
          this.power();
          break;
        }
        case 'rel': {
          this.rel();
          break;
        }
        case 'link': {
          this.link();
          break;
        }
        case 'uni': {
          this.uni();
          break;
        }
        case 'common': {
          this.common();
          break;
        }
        default: {
          if (newArray.test(name)) {
            const group = name.split(' ');
            const index = group[1];
            this.array(index);
            break;
          }
          if (invoke.test(name)) {
            const group = name.split(' ');
            const func = group[1];
            const index = group[2];
            this.invoke(func, index);
            break;
          }
          if (pushSRegexp.test(name)) {
            const value = name.split(' ')[1];
            this.pushS(value);
            break;
          }
          if (pushNRegexp.test(name)) {
            const value = name.split(' ')[1];
            this.pushN(value);
            break;
          }
          if (pushORegexp.test(name)) {
            const value = name.split(' ')[1];
            this.pushO(value);
            break;
          }
        }
      }
    });
    return this.memory.pop();
  }

  /**
   * 
   * @param value
   */
  pushS(value) {
    this.memory.push(value);
  }

  /**
   * 
   * @param value
   */
  pushN(value) {
    value = parseFloat(value);
    this.memory.push(value);
  }

  /**
   * 
   * @param value
   */
  pushO(value) {
    this.memory.push(value);
  }

  /**
   * 
   */
  ifge() {
    const b = this.memory.pop();
    const a = this.memory.pop();
    this.memory.push(a > b);
  }

  /**
   * 
   */
  ifgt() {
    const b = this.memory.pop();
    const a = this.memory.pop();
    this.memory.push(a < b);
  }

  /**
   * 
   */
  ifeq() {
    const b = this.memory.pop();
    const a = this.memory.pop();
    this.memory.push(a === b);
  }

  /**
   * 
   */
  ifne() {
    const b = this.memory.pop();
    const a = this.memory.pop();
    this.memory.push(a !== b);
  }

  /**
   * 
   */
  ificmpge() {
    const b = this.memory.pop();
    const a = this.memory.pop();
    this.memory.push(a >= b);
  }

  /**
   * 
   */
  ificmpgt() {
    const b = this.memory.pop();
    const a = this.memory.pop();
    this.memory.push(a <= b);
  }

  /**
   * 
   */
  add() {
    const b = this.memory.pop();
    const a = this.memory.pop();
    this.memory.push(a + b);
  }

  /**
   * 
   */
  sub() {
    const b = this.memory.pop();
    const a = this.memory.pop();
    this.memory.push(a - b);
  }

  /**
   * 
   */
  mul() {
    const b = this.memory.pop();
    const a = this.memory.pop();
    this.memory.push(a * b);
  }

  /**
   * 
   */
  div() {
    const b = this.memory.pop();
    const a = this.memory.pop();
    this.memory.push(a / b);
  }

  /**
   * mod
   */
  mod() {
    const b = this.memory.pop();
    const a = this.memory.pop();
    this.memory.push(a % b);
  }

  /**
   * 
   */
  common() {
    // TODO ...
    // ....
  }

  /**
   * sheet
   */
  rel() {
    // TODO ...
    // ....
  }

  /**
   * 
   */
  uni() {
    // TODO ...
    // ....
  }

  /**
   * 
   */
  link() {
    const b = this.memory.pop();
    const a = this.memory.pop();
    this.memory.push(`${a}${b}`);
  }

  /**
   * 
   */
  power() {
    const b = this.memory.pop();
    const a = this.memory.pop();
    this.memory.push(Math.pow(a, b));
  }

  /**
   * 
   * @param index
   */
  array(index) {
    const array = [];
    for (let i = 1; i <= index; i++) {
      const item = this.memory.pop();
      array.push(item);
    }
    array.reverse();
    this.memory.push(array);
  }

  /**
   * 
   * @param func
   * @param index
   */
  invoke(func, index) {
    const array = [];
    for (let i = 1; i <= index; i++) {
      const item = this.memory.pop();
      array.push(item);
    }
    array.reverse();
    const execute = Functions.getFunction(func);
    const result = execute.apply(this, array);
    this.memory.push(result);
  }
}

/**
 * 
 * @param input
 * @returns {string|*}
 * @constructor
 */
const Compile = (input) => {
  if (input.startsWith('=')) {
    input = input.substring(1);
  }
  const tokenizer = new Tokenizer();
  const writer = new Assembly();
  const tokens = tokenizer.lexical(input);
  const compiler = new Compiler({
    writer, tokens,
  });
  return compiler.compile();
};

/**
 * 
 * @param inst
 * @returns {*}
 * @constructor
 */
const Instruct = (inst) => {
  const evaluation = new Evaluation(inst);
  return evaluation.eval();
};

/**
 * 
 * @constructor
 */
const Evaluate = input => Instruct(Compile(input));

export {
  Tokenizer,
  Compiler,
  Assembly,
  Compile,
  Evaluate,
  Instruct,
  Functions,
};
