/**
 * 
 */
import { Listen } from '../../../lib/Listen';

class Snapshot {

  /**
   * Snapshot
   */
  constructor() {
    // 
    this.undoStack = [];
    // 
    this.redoStack = [];
    // 
    this.layer = {
      event: '',
      data: [],
    };
    // 
    this.apply = false;
    // 
    this.listen = new Listen();
  }

  /**
   * 
   */
  undo() {
    const layer = this.undoStack.pop();
    for (let i = layer.data.length - 1; i >= 0; i--) {
      const action = layer.data[i];
      action.undo();
    }
    this.redoStack.push(layer);
    this.listen.execute('change', layer.event, 'undo');
  }

  /**
   * 
   */
  redo() {
    const layer = this.redoStack.pop();
    for (let i = 0, len = layer.data.length; i < len; i++) {
      const action = layer.data[i];
      action.redo();
    }
    this.undoStack.push(layer);
    this.listen.execute('change', layer.event, 'redo');
  }

  /**
   * 
   */
  open() {
    this.layer = {
      event: '',
      data: [],
    };
    this.apply = true;
  }

  /**
   * 
   * @returns {boolean}
   */
  canRedo() {
    return this.redoStack.length > 0;
  }

  /**
   * 
   * @returns {boolean}
   */
  canUndo() {
    return this.undoStack.length > 0;
  }

  /**
   * 
   */
  close(event) {
    if (this.layer.data.length) {
      this.layer.event = event;
      this.redoStack = [];
      this.undoStack.push(this.layer);
    }
    this.layer = {
      event: '',
      data: [],
    };
    this.apply = false;
    this.listen.execute('change', event, 'close');
  }

  /**
   * 
   * @param action
   */
  addAction(action = {
    undo: () => {},
    redo: () => {},
  }) {
    if (this.apply) {
      this.layer.data.push(action);
    }
  }
}

export {
  Snapshot,
};
