import { Compile, Instruct } from './Compiler';
import { SheetUtils } from '../utils/SheetUtils';

/**
 * Formula
 */
class Formula {

  /**
   * Formula
   * @param expr
   */
  constructor({
    expr = SheetUtils.EMPTY,
  } = {}) {
    this.expr = expr;
    this.content = SheetUtils.Nul;
    this.instruct = SheetUtils.Nul;
  }

  /**
   * 
   * @return {string}
   */
  getValue() {
    let { expr } = this;
    let { content } = this;
    let { instruct } = this;
    if (!content) {
      if (!instruct) {
        instruct = Compile(expr);
      }
      content = Instruct(instruct);
    }
    this.content = content;
    this.instruct = instruct;
    return content;
  }

  /**
   * 
   * @return {boolean}
   */
  hasExpr() {
    let { expr } = this;
    return !SheetUtils.isBlank(expr);
  }

  /**
   * 
   * @return {string}
   */
  getExpr() {
    return this.expr;
  }

  /**
   * 
   * @param expr
   */
  setExpr(expr) {
    this.expr = expr;
    this.content = SheetUtils.Nul;
    this.instruct = SheetUtils.Nul;
  }

  /**
   * 
   * @returns {Formula}
   */
  clone() {
    return new Formula({
      expr: this.expr,
    });
  }

}

Formula.EMPTY = new Formula();

Formula.getInstance = (config) => {
  if (config) {
    return new Formula(config);
  }
  return Formula.EMPTY;
};

export {
  Formula,
};
